.footer {
  color: white;
  background-color: #262222;
  position: relative;
  width: 100%;
  margin-top: 20%;
  padding: 2em 2em 1em;
}

.column {
  display: inline-block;
  padding-left: 3em;
}

.row hr {
  width: 90%;
}

.list {
  list-style-type: none;
  vertical-align: top;
  padding-right: 3em;
  line-height: 1.6;
}

.list a {
  color: white;
  text-decoration: none;
}

.list a:hover {
  color: yellow;
  text-decoration: underline;
}

.colsm {
  text-align: center;
}
