.NavigationItem {
  display: flex;
  height: 30%;
  width: 100%;
  text-decoration: none;
  left: 15px;
}

.NavigationItem a {
  font-size: 1.2em;
  display: inline-flex;
  padding-top: 7px;
  padding-left: 15px;
  align-items: center;
  flex-wrap: wrap;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  background-color: #d3d3d3;
  border-bottom: 3px solid #304671;
  color: rgb(126, 101, 101);
}
